<template>
  <div class="car-space">
    <ul class="space-list">
      <li v-for="(item,index) in workspaceList">
        <span class="name">{{item.name}}</span>
        <div class="operator">
          <span class="del" @click="deleteWorkshop(item.id)"></span>
          <span class="edit" @click="editWorkshop(item)"></span>
        </div>
      </li>
    </ul>
    <div class="mask" v-if="showMask"></div>
    <div class="mask-box" v-if="showMask">
      <p class="mask-tit">修改车间名称</p>
      <input class="mask-input" v-model="workName" placeholder="请输入车间名称" type="text" name="" value="">
      <ul class="bottombtn-list">
        <li @click="hideWorkshop()">取消</li>
        <li @click="confirmEdit()">确认</li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: 'carSpace',
  data () {
    return {
      workspaceList:[],
      showMask:false,
      workName:'',
      editItem:{},
      editFlag:-1
    }
  },
  methods:{
    getWorkshopList(that){
      that.$ajax({
        method: 'GET',
        url:`${that.api}/api/workshop?page=0&size=100&isDelete=0&sort=createTime,desc`
       }).then(function (response) {
         that.workspaceList=response.data.data.content
       }).catch(function (error) {
          console.log(error);
       });
    },
    deleteWorkshop(id){
      var that=this;
      that.$ajax({
        method: 'delete',
        url:`${that.api}/api/workshop/${id}`
       }).then(function (response) {
         if(response.data.code==0){
           that.getWorkshopList(that)
         }
         console.log(response);
       }).catch(function (error) {
          console.log(error);
       });
    },
    editWorkshop(item){
      this.editFlag=1
      this.showMask=true
      this.editItem=item
      this.workName=item.name
    },
    hideWorkshop(){
      this.showMask=false
    },
    showAdd(){
      this.editFlag=-1
      this.showMask=true
      this.workName=''
    },
    confirmEdit(){
      var that=this;
      if(that.workName==''){
        alert("车间名称不为空")
        return ;
      }else{
        if(this.editFlag==1){
          that.workspaceList.map((item,index)=>{
            if(item.id==that.editItem.id){
              item.name=that.workName
            }
          })
          that.$ajax({
            method: 'PUT',
            url:`${that.api}/api/workshop/all`,
            data:that.workspaceList,
           }).then(function (response) {
             that.workName=''
             that.showMask=false
             console.log(response.data.data);
             //window.webkit.messageHandlers.submitClick.postMessage(JSON.stringify(response.data.data))
           }).catch(function (error) {
              console.log(error);
           });
        }
        if(this.editFlag==-1){
          that.$ajax({
            method: 'post',
            url:`${that.api}/api/workshop`,
            data:{
              'status':0,
              'name':that.workName
            },
           }).then(function (response) {
             that.workName=''
             that.showMask=false
             that.getWorkshopList(that)
             //window.webkit.messageHandlers.submitClick.postMessage(JSON.stringify(response.data.data))
           }).catch(function (error) {
              console.log(error);
           });
        }
      }
    }
  },
  created(){
    this.getWorkshopList(this)
  }
}
</script>



