import Vue from 'vue'
import Router from 'vue-router'
import taskList from '@/view/taskList'
import memberList from '@/view/memberList'
import workshopState from '@/view/workshopState'
import carSpace from '@/view/carSpace'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/record',
      name: 'taskList',
      component: taskList,
      meta: {
          title: '操作记录'
      }
    },
    // {
    //   path: '/',
    //   redirect:{ name: 'taskList' }
    // },
    {
      path: '/member',
      name: 'memberList',
      component: memberList,
      meta: {
          title: '成员管理'
      }
    },
    {
      path: '/workshop',
      name: 'workshopState',
      component: workshopState,
      meta: {
          title: '选择当前车间状态'
      }
    },
    {
      path: '/carspace',
      name: 'carSpace',
      component: carSpace,
      meta: {
          title: '管理车间'
      }
    }
  ]
})



